.open-menu {
  -webkit-animation: openMenu 0.8s ease-in-out;
          animation: openMenu 0.8s ease-in-out; }

@-webkit-keyframes openMenu {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes openMenu {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.menu-back__open {
  -webkit-animation: menuBackOpen 0.8s ease-in-out;
          animation: menuBackOpen 0.8s ease-in-out; }

@-webkit-keyframes menuBackOpen {
  from {
    opacity: 0; }
  to {
    opacity: 0.90; } }

@keyframes menuBackOpen {
  from {
    opacity: 0; }
  to {
    opacity: 0.90; } }

.feedback {
  position: fixed;
  width: 320px;
  height: 100%;
  top: 0;
  left: auto;
  right: 0;
  overflow-y: scroll;
  overflow-x: hidden;
  scrollbar-width: none;
  background: #ffff;
  box-shadow: -44px 5px 41px -4px rgba(41, 42, 43, 0.2);
  z-index: 6; }
  .feedback__connection {
    margin: 24px 16px; }
    .feedback__connection--img {
      margin-top: 10px; }
    .feedback__connection--text {
      color: #1B1C21;
      font-family: "TT Lakes-Bold", sans-serif;
      font-size: 24px;
      line-height: 32px;
      letter-spacing: -0.6px;
      padding: 24px 12px 0 12px; }
      .feedback__connection--text::before {
        content: "";
        position: absolute;
        height: 40px;
        width: 4px;
        left: 0;
        background-color: #41F6D7;
        border-radius: 0px 10px 10px 0px; }
  .feedback__form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 16px; }
    .feedback__form--name {
      width: 99%;
      height: 48px;
      border: 1px solid #EAEAEA;
      border-radius: 10px;
      padding-left: 10px; }
      .feedback__form--name:hover {
        transition: 0.2s;
        border: 1px solid #B8FFEC; }
    .feedback__form--phone {
      width: 99%;
      height: 48px;
      border: 1px solid #EAEAEA;
      border-radius: 10px;
      padding-left: 10px; }
      .feedback__form--phone:hover {
        transition: 0.2s;
        border: 1px solid #B8FFEC; }
    .feedback__form--email {
      width: 99%;
      height: 48px;
      border: 1px solid #EAEAEA;
      border-radius: 10px;
      padding-left: 10px; }
      .feedback__form--email:hover {
        transition: 0.2s;
        border: 1px solid #B8FFEC; }
    .feedback__form--message {
      width: 99%;
      height: 188px;
      border: 1px solid #EAEAEA;
      border-radius: 10px;
      padding: 10px 10px; }
      .feedback__form--message:hover {
        transition: 0.2s;
        border: 1px solid #B8FFEC; }
  .feedback__send {
    margin-left: 16px;
    margin-bottom: 32px;
    text-align: left; }
    .feedback__send--text {
      padding-bottom: 89px;
      font-family: "TT Lakes-Regular", sans-serif;
      size: 14px;
      height: 16px;
      color: #7E7E82; }
    .feedback__send--text-pink {
      color: #FF3E79; }
    .feedback__send--button {
      display: flex;
      align-items: center;
      margin-left: auto;
      margin-right: 16px;
      background-color: #FF3E79;
      width: 136px;
      height: 32px;
      border-radius: 20px; }
      .feedback__send--button:hover {
        transition: 0.5s;
        transform: scale(0.95);
        box-shadow: 0 2px 25px rgba(255, 0, 130, 0.2); }
    .feedback__send--button-text {
      font-family: "TT Lakes-Medium", sans-serif;
      position: relative;
      text-transform: uppercase;
      color: #ffff;
      font-size: 12px;
      line-height: 24px;
      letter-spacing: -0.15px;
      font-weight: 700;
      width: 67px;
      left: 12px;
      top: 0px; }
      .feedback__send--button-text::after {
        content: "";
        position: absolute;
        border-color: #ffff;
        border-style: solid;
        border-width: 0em 0.2em 0.2em 0em;
        height: 0.5em;
        top: 1.5em;
        left: 6.8em;
        margin-top: -1em;
        transform: rotate(-45deg);
        width: 0.5em; }

@media (min-width: 1120px) {
  .feedback {
    width: 440px;
    height: 100%;
    border-left: 1px solid #B8FFEC; }
    .feedback__connection {
      display: flex;
      align-items: center;
      margin-top: 0;
      margin-left: -4em; }
      .feedback__connection--close {
        position: fixed;
        width: 40px;
        height: 40px;
        top: 10px;
        z-index: 8; }
      .feedback__connection--text {
        margin-left: 65px; }
      .feedback__connection--text::before {
        content: "";
        position: absolute;
        height: 40px;
        width: 4px;
        right: 15.6em;
        left: 0;
        background-color: #41F6D7;
        border-radius: 0px 10px 10px 0px; }
    .feedback__form {
      padding-left: 32px; }
    .feedback__send {
      padding-left: 32px; } }

